/* mainVisual
  ======================================== */

.mv{  
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  background-position: center;
  @include PC {
    height: 745px;
    background-image: url(../img/anne/201811/anne_mv.png);
  }
  @include SP {
    height: calc(100vh - 43px);
    background-image: url(../img/anne/201811/anne_mv_sp.png);
  }
  div{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    &.mv_starring{
      @include PC {margin-top: 170px;}
      @include SP {
        width: 72.5%;
        margin-top: 170px;
      }
    }
    &.mv_title{
      @include PC {margin-top: 30px;}
      @include SP {
        width: 75%;
        margin-top: 70px;
      }
    }
  }
  .sprite{
    display: flex;
    background-image: url(../img/anne/201811/anne_mv_menu_bg.png);
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 15px;
    @include PC {
      width: 426px;
    }
    @include SP {
      width: 64%;
      background-size: contain;
    }
    li{
      a{        
        display:block;
        background-image: url(../img/anne/201811/anne_mv_menu_spri.png);
        background-repeat: no-repeat;
        @include PC {height: 37px;}
        @include SP {
          height: 22px;
          background-size: 240px;
        }
      }
      &.sprite-icon01{
        a{
          @include PC {
            width: 93px;
            &:hover{
              background-position: 0px -38px;
            }
          }
          @include SP {
            width: 45px;
          }
        }
      }
      &.sprite-icon02{
        a{
          @include PC {
            width: 93px;
            background-position: -92px 0;
            &:hover{
              background-position: -92px -38px;
            }
          }
          @include SP {
            width: 58px;
            background-position: -47px 0px;
          }
        }
      }
      &.sprite-icon03{
        a{
          @include PC {
            width: 124px;
            background-position: -183px 0;
            &:hover{
              background-position: -183px -38px;
            }
          }
          @include SP {
            width: 63px;
            background-position: -102px 0;
          }
        }
      }
      &.sprite-icon04{
        a{
          @include PC {
            width: 116px;
            background-position: -306px 0;
            &:hover{
              background-position: -306px -38px;
            }
          }
          @include SP {
            width: 74px;
            background-position: -164px 0;
          }
        }
      }
    }
  }
}

