/* anneBlock03
  ======================================== */

.anneBlock03 {
  min-width: 984px;
  padding: 100px 0 69px;
  background-color: #000;
  color: #fff;
  > .inner {
    width: 984px;
    margin: 0 auto;
    padding: 0 69px;
    box-sizing: border-box;
  }
  .title {
    margin-bottom: 50px;
  }
  .story_inner {
    overflow: hidden;
    margin-bottom: 50px;
    .story_img {
      width: 446px;
    }
    .story_txt {
      color: #fff;
      font-size: 16px;
      line-height: 2.6em;
      margin-top: -0.7em;
    }
    &.wrap01{
      .story_img {
        @include PC {float: left;}
      }
      .story_txt {
        @include PC {float: right;}
      }
    }
    &.wrap02{
      .story_img {
        @include PC {float: right;}
      }
      .story_txt {
        @include PC {
          float: left;
          text-align: right;
          width: 45.6%;
        }
        @include SP {
          text-align: left;
        }
        strong{
          line-height: 1em;
          display: block;
          @include PC {
            font-size: 28px;
            margin-top: 45px;
          }
          @include SP {
            font-size: 16px;
            margin-top: 30px;
          }
        }
      }
    }
  }
  .story_next {
    text-align: center;
    font-size: 29px;
    position: relative;
    letter-spacing: 0.05em;
    display: inline-block;
    padding: 0 25px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    &::before, &::after {
      content: '';
      position: absolute;
      top: 50%;
      display: inline-block;
      width: 20px;
      height: 1px;
      background-color: #fff;
    }
    &::before {
      left: 0;
    }
    &::after {
      right: 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .anneBlock03 {
    min-width: 0;
    min-width: initial;
    padding: 45px 0 18px;
    > .inner {
